.infoWrap {
    display: flex;
    flex-direction: row-reverse;
    border-top: solid 1px #828282;
    padding: 5px;
}

.mainWrap {
    display: flex;
    flex-direction: row;
    border-top: solid 1px #828282;
    border-bottom: solid 1px #828282;
    position: relative;
}

@media (max-width: 600px) {
    .mainWrap {
      flex-direction: column;
    }
}
  